<script>
import { mapState } from 'vuex'
import ContainerMixin from '@/components/containerMixin'
import loadIcon from '@/assets/icons'
import { datetime } from '@/utils/helper.js'
import _common from '@style/views/responsible.modules.scss'

export default {
  name: 'Responsible',
  mixins: [ContainerMixin],
  data() {
    return {}
  },
  created() {},
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType']),
    ...mapState('responsible', [
      'responsibleSwitch',
      'headContent',
      'agreementUrl',
      'remindItem',
      'limitItem',
      'dayPlayItem',
      'lossLimitItem'
    ]),
    // 是否设置了提醒时间
    isRemindTime() {
      const { reminder_total_time = 0 } = this.remindItem
      return reminder_total_time && Number(reminder_total_time) > 0
    },
    // 提醒功能是否可编辑
    isRemindEdit() {
      const { edit_cd_time = 0 } = this.remindItem
      return Number(edit_cd_time) <= 0
    },
    // 计算进度条的长度
    sliderWidth() {
      const { reminder_total_time = 0, reminder_time = 0 } = this.remindItem
      let slider = reminder_total_time - reminder_time
      slider = slider > 0 ? slider : 0
      const width = (slider / reminder_total_time) * 100

      return { width: `${width}%` }
    },
    // 限额功能是否可编辑
    isLossLimitEdit() {
      const { edit_cd_time = 0 } = this.lossLimitItem
      return Number(edit_cd_time) <= 0
    },
    // 每日游玩时间功能是否可编辑
    isDailyPlayEdit() {
      const { edit_cd_time = 0 } = this.dayPlayItem
      return Number(edit_cd_time) <= 0
    },
    dailyPlayText() {
      const data = this.dayPlayItem?.day_play_time || {}
      if (data.start_hour && data.start_min && data.end_hour && data.end_min) {
        return `${data.start_hour}:${data.start_min}~${data.end_hour}:${data.end_min}`
      }
      return ''
    }
  },
  watch: {
    // 功能总开关 1开 0关
    responsibleSwitch(n) {
      if (n == 1) return

      this.$router.push({ name: 'menu' })
      this.$pop.message().show({ content: this.$t('UI.Resp_Close_Message_Text') })
    }
  },
  methods: {
    commonStyle: k => _common[k],
    loadIcon: k => loadIcon(k, false),
    // 提醒时间显示格式
    remindTimeFilt(seco = 0) {
      return datetime.getFormatTime(seco)
    },
    // 提醒功能是否可编辑时间格式
    remindEditFilt(seco = 0) {
      return datetime.getFormatTimeTwo(seco)
    },
    // 返回
    onBack() {
      this.$router.back()
    },
    // 跳转外链
    onOutside(name = '') {
      let url = ''

      switch (name) {
        case 'gaming':
          url = 'https://gamblersanonymous.org'
          break
        case 'gamcare':
          url = 'https://gamcare.org.uk'
          break
        case 'term':
          url = this.agreementUrl
          break
      }

      if (url) setTimeout(() => window.open(url, '_blank'))
    },
    // 提醒功能设置时间
    onRemindSet() {
      this.$pop.responRemind().show()
    },
    // 删除提醒功能设置
    onRemindDelete() {
      this.$pop
        .confirm({
          title: this.$t('UI.Responsible_Confirmation'),
          content: this.$t('UI.Responsible_Confirmation_Text'),
          confirmText: this.$t('UI.Responsible_Dialog_Yes'),
          cancelText: this.$t('UI.Responsible_Dialog_No'),
          success: async () => {
            const params = {
              token: this.user.token,
              language: this.languageType,
              time: 0
            }

            const res = await this.$store.dispatch('responsible/deleteReminderTime', params).catch(e => e)
            if (res.code != 0) return this.$pop.message().show({ content: res.msg })

            this.$pop.message().show({ content: res.msg })
          }
        })
        .show()
    },
    // 限制登录设置
    onLimitSet() {
      this.$pop.responLimit().show()
    },
    // 设置限额
    onLossLimitSet(isEdit = false) {
      this.$pop.responLossLimit().show(isEdit)
    },
    // 删除限额设置
    onLossLimitDelete() {
      this.$pop
        .confirm({
          title: this.$t('UI.Responsible_Confirmation'),
          content: this.$t('UI.Resp_Loss_Limit_Tips_6'),
          confirmText: this.$t('UI.Responsible_Dialog_Yes'),
          cancelText: this.$t('UI.Responsible_Dialog_No'),
          success: async () => {
            const params = {
              token: this.user.token,
              language: this.languageType,
              edit_type: 'del'
            }
            const res = await this.$store.dispatch('responsible/setLoss', params).catch(e => e)
            if (res.code != 0) return this.$pop.message().show({ content: res.msg })

            this.$pop.message().show({ content: res.msg })
          }
        })
        .show()
    },
    // 设置每日游玩时间
    onDailyPlaySet(isEdit = false) {
      this.$pop.responDailyPlay().show(isEdit)
    },
    // 删除每日游玩
    onDailyPlayDel() {
      this.$pop
        .confirm({
          title: this.$t('UI.Responsible_Confirmation'),
          content: this.$t('UI.Resp_Daily_Playtime_Text_3'),
          confirmText: this.$t('UI.Responsible_Dialog_Yes'),
          cancelText: this.$t('UI.Responsible_Dialog_No'),
          success: async () => {
            const params = {
              token: this.user.token,
              language: this.languageType,
              edit_type: 'del'
            }
            const res = await this.$store.dispatch('responsible/setDayPlay', params).catch(e => e)
            if (res.code != 0) return this.$pop.message().show({ content: res.msg })

            this.$pop.message().show({ content: res.msg })
          }
        })
        .show()
    }
  }
}
</script>

